import React, { RefObject } from "react";
import {
  BrowserRouter as Router,
  Route,
  useLocation,
  Switch,
} from "react-router-dom";
import "./app.css";
import NavBar from "./components/NavBar";
import About from "./components/About";
import Header from "./components/Header";
import PickupStore from "./components/PickupStore";
import Dashboard from "./components/Dashboard";

interface IAppProps {}

const App: React.FC<IAppProps> = (props) => {
  const containerRef = React.useRef<HTMLDivElement>(null);
  const navHeighInEm = React.useRef(null);
  const mq = window.matchMedia("max-width: 600px");
  let pageLinksDiv: any;

  React.useEffect(() => {
    alert(containerRef.current?.querySelector<HTMLDivElement>("div"));

    mq.addEventListener("change", (e) => {
      if (e.matches) {
        alert("media query changed");
      }
    });

    containerRef.current?.style.setProperty(
      "margin-top",
      `${Number(navHeighInEm.current)}px`
    );
  }, []);

  return (
    <div className="cApp_MainContainer">
      {/* {useLocation().pathname === "/" ? <Header></Header> : null} */}
      <NavBar parentRef={navHeighInEm}></NavBar>
      <div className="cApp_ContentPresenter" ref={containerRef}>
        <Switch>
          <Route exact path={"/"} component={Dashboard} />
          <Route path={"/about"} component={About} />
          <Route path={"/pickupstore"} component={PickupStore} />
        </Switch>
      </div>
    </div>
  );
};

export default App;
