import * as React from "react";

interface IGMapContainerProps {
  mapWidth: number;
  mapHeight: number;
  measureUnits: string;
}

let googleMapRef: any = React.createRef<HTMLDivElement>();
const GOOGLE_MAP_API_KEY: string = "AIzaSyBVCIhFavhUtx6dfpa9-9Hz0KxkaFbGzEU";
let googleMap: any;

interface IMarker {
  lat: number;
  lng: number;
  name: string;
  label?: string;
}
const markers: IMarker[] = [
  { lat: 11.847263, lng: -86.192927, name: "Gü" },
  { lat: 11.849684, lng: -86.19883, name: "CB" },
  { lat: 11.848625, lng: -86.19852, name: "CS" }
];

const createGoogleMap = () => {
  return new window.google.maps.Map(googleMapRef.current, {
    zoom: 15,
    center: {
      lat: 11.847931,
      lng: -86.199168
    },
    disableDefaultUI: true
  });
};

const createMarker = (props: IMarker) => {
  const { lat, lng, name, label } = props;

  new window.google.maps.Marker({
    position: { lat: lat, lng: lng },
    map: googleMap,
    label: name,
    animation: google.maps.Animation.DROP
  });
};

const drawMap = () => {
  googleMap = createGoogleMap();

  if (googleMap) {
    markers.map(m => {
      createMarker(m);
    });
  } else alert("Map object is undefined");
};

const GMapContainer: React.FC<IGMapContainerProps> = props => {
  const { mapWidth, mapHeight, measureUnits } = props;

  React.useEffect(() => {
    const apiURL: string = `https://maps.googleapis.com/maps/api/js?key=${GOOGLE_MAP_API_KEY}&libraries=places`;

    /* Check if gMapScript has been loaded added to the document. Multiple instances of the script can cause trouble */
    let gMapScript: HTMLScriptElement | null = document.querySelector<
      HTMLScriptElement
    >(`script[src="${apiURL}"]`);

    /* If the GMapScript hasn't been loaded, created using apiURL and append it to the body. Call drawMap function for the 
    first time when the script is fully loaded */

    if (!gMapScript) {
      gMapScript = document.createElement("script");
      gMapScript.src = apiURL;
      window.document.body.appendChild(gMapScript);
      gMapScript.addEventListener("load", drawMap);
    } else {
      /* Call drawMap function every time the component is render, if not the map wont be draw on the div */
      drawMap();
    }

    /* Remove load event listener for gMapScript */
    return () => {
      gMapScript?.removeEventListener("load", drawMap);
    };
  }, []);

  const mapStyle: any = {
    width: `${mapWidth + measureUnits}`,
    height: `${mapHeight + measureUnits}`
  };

  return <div id="google-map" ref={googleMapRef} style={mapStyle}></div>;
};

export default GMapContainer;
